import './new.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'lil-gui'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'



/**
 * Base
 */
// Debug
/* const gui = new dat.GUI() */

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color( 0x000000 );

// Models
const gltfLoader = new GLTFLoader()

let mixer = null

gltfLoader.load(
    '/simulation/3D/models/plaza/plaza.gltf',
    (gltf) =>
    {       

        mixer = new THREE.AnimationMixer(gltf.scene)
        mixer.timeScale = 10

        const action = mixer.clipAction(gltf.animations[0])
        const action1 = mixer.clipAction(gltf.animations[1])
        const action2 = mixer.clipAction(gltf.animations[2])
        const action3 = mixer.clipAction(gltf.animations[3])
        const action4 = mixer.clipAction(gltf.animations[4])
        const action5 = mixer.clipAction(gltf.animations[5])
        const action6 = mixer.clipAction(gltf.animations[6])
        const action7 = mixer.clipAction(gltf.animations[7])
        const action8 = mixer.clipAction(gltf.animations[8])
        const action9 = mixer.clipAction(gltf.animations[9])
        const action10 = mixer.clipAction(gltf.animations[10])
        const action11 = mixer.clipAction(gltf.animations[11])
        const action12 = mixer.clipAction(gltf.animations[12])
        const action13 = mixer.clipAction(gltf.animations[13])
        const action14 = mixer.clipAction(gltf.animations[14])
        const action15 = mixer.clipAction(gltf.animations[15])
        const action16 = mixer.clipAction(gltf.animations[16])
        const action17 = mixer.clipAction(gltf.animations[17])
        const action18 = mixer.clipAction(gltf.animations[18])
        const action19 = mixer.clipAction(gltf.animations[19])
        const action20 = mixer.clipAction(gltf.animations[20])
        const action21 = mixer.clipAction(gltf.animations[21])
        const action22 = mixer.clipAction(gltf.animations[22])
        const action23 = mixer.clipAction(gltf.animations[23])

        /* console.log(gltf) */

        action.play()
        action1.play()
        action2.play()
        action3.play()
        action4.play()
        action5.play()
        action6.play()
        action7.play()
        action8.play()
        action9.play()
        action10.play()
        action11.play()
        action12.play()
        action13.play()
        action14.play()
        action15.play()
        action16.play()
        action17.play()
        action18.play()
        action19.play()
        action20.play()
        action21.play()
        action22.play()
        action23.play()

        gltf.scene.scale.set(0.1, 0.1, 0.1)
        scene.add(gltf.scene)
    }
)




/**
 * Lights
 */
const ambientLight = new THREE.AmbientLight(0xffffff, 1.0)
scene.add(ambientLight)

const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6)
directionalLight.castShadow = true
directionalLight.shadow.mapSize.set(1024, 1024)
directionalLight.shadow.camera.far = 15
directionalLight.shadow.camera.left = - 7
directionalLight.shadow.camera.top = 7
directionalLight.shadow.camera.right = 7
directionalLight.shadow.camera.bottom = - 7
directionalLight.position.set(5, 5, 5)
scene.add(directionalLight)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(2, 2, 2)
scene.add(camera)


// Controls
const controls = new OrbitControls(camera, canvas)
controls.target.set(0, 0.75, 0)
controls.enableDamping = true

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,

})
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))





























/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    // Update mixer
    if(mixer !== null)
    {
        mixer.update(deltaTime)
    }


    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()